*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
.mainbox{
  width: 100%;
  
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.whather{
  /* width: 30%; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  
  border: 1px solid rgb(0, 9, 12);
  padding: 45px;
  /* margin: 50%; */
  border-radius: 18px;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: rgba(11, 44, 194, 0.4);
}
.sss{
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid white;
  padding-bottom: 5px;
}






@media screen and( max-width :765px) {
  .whather{
    width: 50%;  
  }
}

.c{
  opacity: 0.6;
}